import React from 'react';
import styled from 'styled-components';
import { device } from '../assets/data/mediaQueries';
import { uid } from 'react-uid';
import Logo from '../atoms/Logo';
import Title from '../atoms/Title';


const PopupWithOrderScreen = ({ togglePopup, newBurger, currentPrice, ingredients }) => {

  const mapIngredients = Object.entries(ingredients);
  const info = mapIngredients.filter(item => item[1] !== 0).map(ingredient => (
    <p key={uid(ingredient)}>{`${ingredient[0]}: ${ingredient[1]}`}</p>
  ));

  const updatePrice = price => price.toFixed(2)
  return (
    <Wrapper>
      <WrapperInner>
        <Title>We received your order!</Title>
        <p>Your ingredients: {info}</p>
        <p>{`Prepare to pay: ${updatePrice(currentPrice)} £`}</p>
        <button onClick={togglePopup}>X</button>
        <button onClick={newBurger}>Create a new burger</button>
        <Logo />
      </WrapperInner>
    </Wrapper>
  )
};

export default PopupWithOrderScreen;

const Wrapper = styled.div`
  background-color: rgba(0,0,0,.8);
  height: 100vh;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1000;
  overflow: hidden;
`;

const WrapperInner = styled.div`
  background-image: radial-gradient(#5C5C5C , #000000);
  height: 70vh;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;

  @media ${device.tabletXL} {
    height: 100vh;
    width: 100%;
  } 
`;